.button {
  display: inline-block;
  min-width: 160px;
  padding: 12px 17px;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.25;
  color: $primary-black-color;

  border: none;
  border-radius: 22px;
  background-color: $primary-white-color;
  cursor: pointer;

  transition: background-color 250ms $timing-function;

  &--primary {
    color: $primary-white-color;
    background-color: $accent-color;

    &:hover,
    &:focus {
      background-color: $primary-btn-hover-color;
    }
  }

  &--secondary {
    color: $accent-color;
    background-color: $primary-white-color;

    &:hover,
    &:focus {
      background-color: $secondary-btn-hover-color;
    }
  }

  &--with-icon {
    @include centeredFlex($type: inline-flex, $main-axis: center);
  }
}

.button__icon {
  margin-left: 10px;

  .button--primary & {
    stroke: $primary-white-color;
  }

  .button--secondary & {
    stroke: $accent-color;
  }
}
